import { store } from "../app";
import cartProduct from "./cartProduct";
export default function cartPage() {
    var cartProducts = store.cart.products;
    var cartProductsHTML = "";
    var cartProductsCounter = 0;
    var cartProductsTotal = 0;
    if (cartProducts.length == 0) {
        cartProductsHTML = /* html */ "<h2 class=\"brand-title\">You have nothing in your cart</h2>";
    }
    else {
        cartProducts.forEach(function (product) {
            cartProductsHTML += cartProduct(product);
            cartProductsCounter += product.qty;
            cartProductsTotal += product.price * product.qty;
        });
    }
    return /* html */ "\n    <section class=\"section cart d-flex flex-col\">\n        <h4 class=\"d-flex j-space-between a-items-center cart-header\">\n            <span><img src=\"img/mushi.ico\" alt=\"Mushi logo\" class=\"cart-logo\"></span>\n            <span class=\"cart-title\">Your cart</span>\n            <span class=\"cart-counter\">" + cartProductsCounter + "</span>\n        </h4>\n        <ul class=\"cart-list a-self-center d-flex flex-col\">\n            " + cartProductsHTML + "\n            <li class=\"cart-item total-price d-flex j-space-between\">\n                <p>Total (ARS)</p>\n                <p>" + cartProductsTotal.toLocaleString('es-AR', { style: 'currency', currency: 'ARS' }) + "</p>\n            </li>\n        </ul>\n        <div class=\"d-flex j-center\">\n            <button class=\"btn\" id=\"confirmOrder\">Confirm my order</button>\n        </div>\n    </section>\n    ";
}
